html,
body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.container {
  display: flex;
  flex-direction: column;
  width: fit-content;
}
.preview {
  height: 500px;
  aspect-ratio: 1;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}
p {
  margin: 0;
}
.result .lines {
  border: 1px solid #777;
}
